import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, LoadingController, ModalController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/apicall.service';
import { GlobalService } from 'src/app/services/global.service';
import { ToastService } from 'src/app/services/toast.service';
import { ModelPage } from '../model/model.page';

@Component({
  selector: 'app-add-mr',
  templateUrl: './add-mr.page.html',
  styleUrls: ['./add-mr.page.scss'],
})
export class AddMrPage implements OnInit {
  public filter_data:any;
  public btnValue : any = "Add MR";
  slectTabs= 'addMr'
  slectTab='Edit'
  public mrlist : any = ['Dr.Name1' , 'Dr.Name2' , 'Dr.Name3' , 'Dr.Name4' , 'Dr.Name5']
  public cred = { Name : '' , Fname : '' , Contact : null , email : '' , city : ''}
  public allMR:any;
  public allMRlogin:any;
  Togglevaluee: any  = 'deactive';
  public Togglecheked: any;
  public mrData : any = {name: '', contact_no: '', email: '', login_id: '', password: '', status:'' }
  public mrloginstatus : any = {mr_id: '', status: '' }
  constructor(public global:GlobalService , public apicall : ApicallService, public toast : ToastService , public route : Router, private loadingCtrl: LoadingController, private modalController: ModalController, private alertController: AlertController) { }

  ngOnInit() {
    this.getmrData();
    this.getallmrData();
  }

  ShowHidePassword(ele, eye) {
    if (ele.type === 'password') {
      ele.type = 'text';
      eye.name = 'eye-off-outline'
    }
    else {
      ele.type = 'password';
      eye.name = 'eye-outline'
    }
  }

  async changeToggle($event: CustomEvent) {
    console.log($event.detail.checked);
    console.log($event.detail.value);
    if ($event.detail.checked == true) {
          this.Togglevaluee = 'active'
          console.log(this.mrData.status);
    }
    else{
      this.Togglevaluee = 'deactive'
      console.log(this.mrData.status);
    }
  }

  async updatemrStatus($event: CustomEvent) {
    console.log($event.detail.checked);
    console.log($event.detail.value);
    if ($event.detail.checked == true) {
      this.mrloginstatus.mr_id = $event.detail.value.id
      const x = 'active'
      this.mrloginstatus.status = x;
      console.log(this.mrloginstatus.status);
      console.log(this.mrloginstatus);
      await this.apicall.api_updateMRstatus(this.mrloginstatus);
      this.getmrData();
    }
    else{
      this.mrloginstatus.mr_id = $event.detail.value.id
      const x = 'deactive'
      this.mrloginstatus.status = x;
      console.log(this.mrloginstatus.status);
      console.log(this.mrloginstatus);
      await this.apicall.api_updateMRstatus(this.mrloginstatus);
      this.getmrData();
    }
  }

 async addmrData(){
  if(this.btnValue == 'Add MR'){
    console.log(this.mrData)
    this.mrData.status = this.Togglevaluee;
   await this.apicall.api_mrsignup(this.mrData);
    this.mrData = {name: '', contact_no: '', email: '', login_id: '', password: '', status:'' }
    this.getmrData();
  }
  else{
    this.showLoading();
    console.log(this.mrData)
   await this.apicall.api_updateMR(this.mrData);
     this.mrData = {name: '', contact_no: '', email: '', login_id: '', password: ''}
     this.getmrData();
    await setTimeout(() => {
       this.btnValue = "Add MR";
      }, 1000);

  }

  }

  async getmrData(){
   await this.apicall.api_getMR();
    this.global.GetMrData.subscribe(res =>{
      this.allMR = res;
    })
  }

  async delete(mr_id){
    const x = {mr_id:mr_id};
   await this.apicall.api_deleteMR(x);
    this.getmrData();
  }
  
  async update(item){
    this.showLoading();
    setTimeout(() => {
      this.btnValue = "Update MR";
      this.mrData = item;
    }, 1000);
  }

  async getallmrData(){
    await this.apicall.api_getMRloginData();
     this.global.AllmrLoginData.subscribe(res =>{
       this.allMRlogin = res;
     })
   }

   async updateMrloginStatus(item){
    console.log(item);
    if(item.status == 'activate'){
      const x = "deactivate"
      this.mrloginstatus.status = x;
      this.mrloginstatus.mr_id = item.mr_id;
      console.log(this.mrloginstatus);
     await this.apicall.api_updateMRstatus(this.mrloginstatus);
     this.getallmrData();
    }
    if(item.status == 'deactivate'){
      const x = "activate"
      this.mrloginstatus.status = x;
      this.mrloginstatus.mr_id = item.mr_id;
      console.log(this.mrloginstatus);
     await this.apicall.api_updateMRstatus(this.mrloginstatus);
     this.getallmrData();
    }
    else{
      console.log('no user statsu found');
    }
  
   }

  async openModal() {
    const modal = await this.modalController.create({
      component: ModelPage,
      componentProps: {
        'Category': "addMRLogin",
      },
      initialBreakpoint: 0.6,
      breakpoints: [0.1, 0.5, 1]
    });
    return await modal.present();
  }

  async updatemrloginModal(item) {
    const modal = await this.modalController.create({
      component: ModelPage,
      componentProps: {
        'updateMRLogin': item,
        'Category': "updateMRLoginData",
      },
      initialBreakpoint: 0.6,
      breakpoints: [0.1, 0.5, 1]
    });
    return await modal.present();
  }

  async showLoading() {
    const loading = await this.loadingCtrl.create({
      message: 'Loading...',
      duration: 1000,
      spinner: 'circles',
    });

    loading.present();
  }

  async DeleteAlert(mr_id) {
    const alert = await this.alertController.create({
        header: 'Are you sure to delete this MR!',
        buttons: [
            {
                text: 'Cancel',
                role: 'cancel',
                handler: () => {}
            }, {
                text: 'OK',
                role: 'confirm',
                handler: () => {
                  console.log(mr_id);
                  this.delete(mr_id);
                }
            },
        ]
    });

    await alert.present();

    const {role} = await alert.onDidDismiss();
}


}
